import React from 'react'
import {Link} from 'react-router-dom'
import navigation from '../../style/buttons/navigation'
import main from '../../style/main'
import '../../style/buttons/buttons.css'

const NavButton = props => {

    return (
        <div style = {main.row}>
            <Link to={props.link}>
                <div style = {navigation.button} id = {props.id} className="button">
                    <img src={props.img} alt = "" className="button-icon"/>
                </div>
            </Link>
            <div style = {Object.assign({}, main.center, {fontSize:  ".8em"})}>{props.label}</div>
        </div>  
    )
}

export default NavButton