import React from 'react'
import main from '../style/main'
import colors from '../style/colors'

const Projects = () => {
    return(
        <div style = {main.contents}>
            <div style = {Object.assign({}, main.title, {color: colors.red})}>Projects</div>
            <div style = {main.text}> 
                Here are some projects that I've worked on. Feel free to take a look!
            </div>
        </div>
    )
}

export default Projects