import React, {useState} from 'react'
import Router from '../router'
import NavBar from '../components/Navigation/NavBar'
import main from '../style/main'

const Main = () => {

    let [nav, setNav] = useState([
        {label:"About Me",
         img: null,
         id: "yellow"},
        {label:"Projects",
        img: null,
        id: "red"},
        {label:"Hobbies",
        img: null,
        id: "blue"},
    ])

    return (
        <div className="App">
            <div style={main.appLayout}>
                <div style = {Object.assign({}, main.row, {height: "100vh"})}>
                    <NavBar nav = {nav}/>
                    <Router/>
                </div>
            </div>
        </div>
    );
}

export default Main