import React from 'react'
import main from '../style/main'
import colors from '../style/colors'

const About = () => {
    return(
        <div style = {main.contents}>
            <div style = {Object.assign({}, main.title, {color: colors.yellow})}>Hi! I'm Angela.</div>
            <div style = {main.subtitle}> Thanks for visiting my website! </div>
            <div style = {main.text}> 
                My name is Angela Lam and I am an aspiring game developer form Southern California.
                I previously worked as a Software Development Intern at Ardent Labs 
                and as an Instructor for their extracurricular classes at Ardent Academy. 
                I currently work as an IT contractor for the scoring teams at tournaments run by USA Archery.
            </div>
            <div style = {main.text}>
                I am an avid gamer, archer, and creative in my free time and I enjoy travelling and seeing the world. 
                I graduated from UCLA (Go Bruins!) in Summer 2018 with a deree in Computational and Systems Biology and a minor in Mathematics.
            </div>
            <div style = {main.text}>
                I started coding in college, and came to love the challenges that came with it 
                and the satisfaction of finding nice clean solutions to various problems. 
                During my time in college aside from being an active member of the Archery Club, 
                I learned a lot of things and even helped publish a research paper, but some of 
                the most important lessons I learned have been incomrporated into things that I 
                consider a part of my working philosophy today.
            </div>
            <div style = {main.subtitle}> Find Me </div>
            <div style = {main.text}>
                I am currently looking for a job to grow my skills. Whether that's game development, or something else, there is always something new to learn!
                If you would like to talk, whether concerning that, or just have feedback or questions about my website, please feel free to contact me!
                I've provided a couple of different ways to find me on my website.
                Hope to hear from you soon!
            </div>
        </div>
    )
}

export default About