export const main = {
    appLayout: {
        background: "white",
        minHeight: "100vh",
        display: "flex",
        flexDirection: "column",
        justifyContent: "center",
        fontSize: "calc(10px + 2vmin)",
        color: "#282c34",
    },

    center:{
        display: "flex",
        alignItems: "center",
        justifyContent: "center,"
    },

    contents:{
        flexGrow: 1, 
        justifyContent: "left", 
        textAlign: "left", 
        alignItems: "left", 
        paddingLeft: "3em", 
        paddingTop: "3em", 
        paddingRight: "8em"
    },
    
    row: {
        display: "flex",
        flexDirection: "row",
        justifyContent:"space-around",
        width: "100%",
    },

    title: {
        fontSize: "2em",
        marginRight:"10px",
        marginBottom: ".5em"
    },

    subtitle: {
        fontSize: "1.2em",
        marginTop: ".5em",
        marginBottom: ".5em"
    },

    text: {
        fontSize: ".8em",
        marginTop: ".5em",
        marginBottom: ".5em"
    }
}

export default main