import React from 'react'
import {withRouter} from 'react-router-dom'
import NavButton from './NavButton'
import navigation from '../../style/buttons/navigation'
import main from '../../style/main'
import about from '../../icons/about.svg'
import projects from '../../icons/projects.svg'
import hobbies from '../../icons/hobbies.svg'
import resume from '../../icons/resume.svg'
import pdf from '../../docs/2019 Angela Lam Resume.pdf'
import '../../style/buttons/buttons.css'

const NavBar = () => {
    
    let nav=[
        {label:"About Me",
        src: about,
        id: "yellow",
        link: "/about"},
        {label:"Projects",
        src: projects,
        id: "red",
        link: "/projects"},
        {label:"Hobbies",
        src: hobbies,
        id: "blue",
        link: "/hobbies"},
    ]

    return (
        <div style = {{display: "flex", flexDirection: "column", justifyContent: "center", marginLeft: "10%"}}>
            <div style = {navigation.bar}>
                {nav.map((item) => <NavButton label={item.label} img={item.src} id={item.id} link={item.link}/>)}
                <div style = {main.row}>
                    <a href = {pdf} target = "_blank" rel="noopener noreferrer">
                        <div style = {navigation.button} id = "black" className="button">
                            <img src={resume} alt = "" className="button-icon"/>
                        </div>
                    </a>
                    <div style = {Object.assign({}, main.center, {fontSize:  ".8em"})}>Resume</div>
                </div>
            </div>
        </div>
    );
}

export default withRouter(NavBar)